
img {
  max-width: 100%;
}
.banner img{
  border-radius: 10px;
}
.action-bar {
  padding-top: 10px;
  padding-bottom: 10px;
}
.container {
  padding: 15px;
}
.article-content {
  color: #666;
  font-size: 14px;
}
.form-label {
  font-size: 14px;
  color: rgb(66,66,66);
  padding-top: 10px;
}
.form-input {
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  height: 36px;
  padding: 6px 8px;
  border: 1px solid #ccc;
  color: #555;
  background-color: #fff;
  font-size: 12px;
  width: 100%;
  border-color: rgb(240, 239, 239);
  box-sizing: border-box;
  -webkit-transition: none;
  transition: none;
}
.form-input:focus {
  outline: none;
  box-shadow: inset 0 1px 1px rgba(102,175,233,.3);
  border-color: rgba(102,175,233,.6);
}
.form-textarea {
  height: 100px;
  resize: none;
}
.form-radio {
  vertical-align: text-bottom;
  margin-bottom: 1px;
}
.form-label > .form-radio:first-child {
  margin-left: 20px;
}
.form-radio-label {
  padding-right: 10px;
}
.form-group-padding {
  padding-top: 7px;
}
.botton-box {
  padding-top: 20px;
  padding-bottom: 40px;
}
.file-delete {
  text-decoration: underline;
}



